.message {
    width: 950px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    gap: 10px;
}

.imageAndText img {
    height: 60px;
    width: 60px;
}

.text {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: relative;
}

.text:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #0066CC;
    z-index: -1;

    border-radius: 10px;
}

.name {
    /*font-family: Montserrat;*/
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    text-align: left;

    padding: 0 10px;

    color: #FFFFFF;
}

.messageText {
    color: #FFFFFF;

    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;

    padding: 0 10px;
}

.time {
    font-size: 10px;
    font-weight: 600;
    line-height: 12px;
    text-align: left;

    padding-right: 18px;
}