.sidebar {
    height: 100%;
    width: 250px;
    position: fixed;
    top: 0;
    left: -250px; /* Start hidden off the screen */
    background-color: #FFFFFF;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 100px;
    z-index: 999;



    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.sidebar.open {
    left: 0;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.5s;
    z-index: 1;
}

.nav {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 30px;

}

.nav a {
    text-decoration: none;
    font-family: Montserrat, serif;
    font-weight: 600;
    font-size: 22px;
}

.nav a:hover {
    color: #58ACFF;
    text-decoration: none;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    position: absolute;
    right: 10px;
    top: 10px;
}

