.burgerMenuIcon {
    margin-left: 70px;

}

.header {
    position: relative;

    width: 100%;
    height: 60px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 1px solid black;

}

.header::before {
    content: '';
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    height: 50px; /* Высота области размытия */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0));
    pointer-events: none; /* Псевдоэлемент не будет мешать взаимодействию с контентом */
}


.header h1 {
    margin-right: 70px;
    font-family: Montserrat, serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 30px;
    text-align: right;
}

