.friendMessage {

}

.friendImageAndText {
    display: flex;
    gap: 10px;

}

.friendImageAndText img {
    height: 60px;
    width: 60px;
}

.friendText {
    display: flex;
    flex-direction: column;
    position: relative;
}

.friendText:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(245, 247, 251);
    z-index: -1;

    border-radius: 10px;
}

.friendName {
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    text-align: left;

    padding: 0 10px;

    color: black;
}

.friendMessageText {
    color: black;

    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;

    padding: 0 10px;

}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    line-height: 12px;
    text-align: left;

    padding-left: 18px;
}
